@import '../../styles/colors.scss';

#contactNeon {
  font-size: 3vw;
  font-weight: 200;
  font-style: italic;
  color: #fff;
  padding: 20px;
  border: 0.4rem solid #fff;
  border-radius: 2rem;
  text-transform: uppercase;
  animation: flicker 1.5s infinite alternate;
  text-align: center;
  font-family: 'Exo 2', sans-serif;
  text-decoration: none;
  position: absolute;
  z-index: 10;
  margin: 0 auto;
  top: 10%;
  left: 50%;
  transform: translate(-50%, -10%);
}

#contactNeon::-moz-selection {
  background-color: $neonBorder;
  color: $neonText;
}

#contactNeon::selection {
  background-color: $neonBorder;
  color: $neonText;
}

#contactNeon:focus {
  outline: none;
}

@keyframes flicker {
  0%,
  19%,
  21%,
  23%,
  25%,
  54%,
  56%,
  100% {
    text-shadow: -0.2rem -0.2rem 1rem #fff, 0.2rem 0.2rem 1rem #fff,
      0 0 2rem $neonText, 0 0 4rem $neonText, 0 0 6rem $neonText,
      0 0 8rem $neonText, 0 0 10rem $neonText;

    box-shadow: 0 0 0.5rem #fff, inset 0 0 0.5rem #fff, 0 0 2rem $neonBorder,
      inset 0 0 2rem $neonBorder, 0 0 4rem $neonBorder,
      inset 0 0 4rem $neonBorder;
  }

  20%,
  24%,
  55% {
    text-shadow: none;
    box-shadow: none;
  }
}

@media only screen and (max-width: 1200px) {
  #contactNeon {
    top: initial;
    left: initial;
    transform: none;
    bottom: 20px;
    left: 40px;
    font-size: 40px;
  }
}

@media only screen and (max-width: 550px) {
  #contactNeon {
    font-size: 5vw;
  }
}
