@import '../../styles/colors.scss';

#pContainer {
  padding: 20px;

  #pHeader {
    color: $color-blauet-clar;
    font-family: 'Cantarell', sans-serif;
    font-size: 7em;
    font-weight: bold;
    font-style: italic;
    text-transform: uppercase;
    letter-spacing: 2px;
    -webkit-text-stroke: 6px $color-rosa-clar;
    animation: hotline-miami 3.5s ease-out infinite;
  }

  #pProjects {
    padding-left: 15px;
    display: flex;
    flex-wrap: wrap;

    .projectBlock {
      margin: 30px;
      display: flex;
      flex-direction: column;
      flex: 1 0 40%;

      .projName {
        font-family: Sarina;
        font-size: 32px;
        color: $color-blauet-clar;
        text-shadow: 2px 2px $color-rosa-clar;
      }

      .imgCont {
        display: flex;

        .projDesc {
          color: #fff;
          padding: 10px 0 0 10px;
          flex: 1 1;
          font-family: Ruda;
          font-size: 20px;
          color: #fff;
          max-width: 600px;
        }

        .projImg {
          margin-top: 10px;
          width: 500px;
          flex: 0 1 500px;
          border: 5px solid #fff;
        }
      }

      .plinks {
        margin-top: 5px;

        .aLinks {
          text-decoration: none;
          color: #fff;
          margin-left: 6px;
          font-size: 18px;
          transition: 0.4s;

          .iconL {
            transition: 0.4s;
            size: 15px;
          }

          .codeN {
            margin-left: 4px;
            font-family: Ruda;
          }
        }

        .aLinks:hover {
          color: $color-blauet-clar;
        }

        .aLinks:hover > .iconL {
          color: $color-blauet-clar;
        }
      }

      .forSmaller {
        display: none;
      }
    }
  }
}

@keyframes hotline-miami {
  15% {
    margin-left: 0;
    text-shadow: none;
  }

  45%,
  55% {
    margin-left: 1.25em;
    -webkit-text-stroke: 6px #fff;
    text-shadow: -4px 0 #fff, -8px 0 #fff0f8, -12px 0 #ffe0f0, -16px 0 #ffd6eb,
      -20px 0 #ffc7e4, -24px 0 #ffbddf, -28px 0 #ffb3da, -32px 0 #ffa3d3,
      -36px 0 #ff99ce, -40px 0 #ff8fc9, -44px 0 #ff85c4, -48px 0 #ff75bc,
      -52px 0 #ff6bb7, -56px 0 #ff61b3, -60px 0 #ff57ae, -64px 0 #ff4ba8;
  }
}

@keyframes hotline-miami2 {
  15% {
    margin-left: 0;
    text-shadow: none;
  }

  45%,
  55% {
    margin-left: 1.25em;
    -webkit-text-stroke: 3px #fff;
    text-shadow: -4px 0 #fff, -8px 0 #fff0f8, -12px 0 #ffe0f0, -16px 0 #ffd6eb,
      -20px 0 #ffc7e4, -24px 0 #ffbddf, -28px 0 #ffb3da, -32px 0 #ffa3d3,
      -36px 0 #ff99ce, -40px 0 #ff8fc9, -44px 0 #ff85c4, -48px 0 #ff75bc,
      -52px 0 #ff6bb7, -56px 0 #ff61b3, -60px 0 #ff57ae, -64px 0 #ff4ba8;
  }
}

@media only screen and (max-width: 1900px) {
  #pContainer {
    #pProjects {
      .projectBlock {
        .imgCont {
          .projImg {
            width: 300px;
            flex: 0 1 400px;
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 1700px) {
  #pContainer {
    #pProjects {
      .projectBlock {
        flex: 1 1 50%;
      }
    }
  }
}

@media only screen and (max-width: 1100px) {
  #pContainer {
    #pHeader {
      font-size: 13vw;
      -webkit-text-stroke: 3px $color-rosa-clar;
      animation: hotline-miami2 3.5s ease-out infinite;
    }
  }
}

@media only screen and (max-width: 900px) {
  #pContainer {
    #pProjects {
      .projectBlock {
        margin-bottom: 15px;

        .imgCont {
          .projImg {
            flex: 1 1 100%;
            width: 100%;
          }

          .projDesc {
            display: none;
          }
        }
        .forSmaller {
          padding-top: 10px;
          display: flex;
          color: #fff;
          font-family: Ruda;
          font-size: 20px;
        }
      }
    }
  }
}

@media only screen and (max-width: 500px) {
  #pContainer {
    #pProjects {
      padding-left: 0;

      .projectBlock {
        margin: 30px 0 15px 0;
        flex: 1 1 100%;

        .projName {
          font-size: 25px;
          text-shadow: 2px 2px $color-rosa-clar;
        }

        .imgCont {
          display: block;
          .projImg {
            flex: initial;
            max-width: 100%;
            max-height: 100%;
            margin: 0 auto;
          }
        }
      }
    }
  }
}
