@import "./styles/colors.scss";
@import "./styles/fonts.scss";

html {
  overflow-x: hidden;
  overflow-y: hidden;
}

.App {
  background: $color1;
  position: relative;
}

.equalizer {
  display: none;
  width: 15px;
  background-color: transparent;
  border: 0;
  padding: 0.5em;
  position: fixed;
  transition: opacity 0.5s ease;
  bottom: 30px;
  right: 50px;
  z-index: 20;
}

.equalizer:hover {
  outline: 0;
  opacity: 0.5;
  cursor: pointer;
}

.equalizer span {
  display: inline-block;
  width: 3px;
  background-color: #ddd;
  position: absolute;
  bottom: 0;
}

@keyframes shorteq {
  0% {
    height: 10px;
  }
  50% {
    height: 5px;
  }
  100% {
    height: 10px;
  }
}
@-webkit-keyframes shorteq {
  0% {
    height: 10px;
  }
  50% {
    height: 5px;
  }
  100% {
    height: 10px;
  }
}

@keyframes talleq {
  0% {
    height: 15px;
  }
  50% {
    height: 8px;
  }
  100% {
    height: 15px;
  }
}
@-webkit-keyframes talleq {
  0% {
    height: 15px;
  }
  50% {
    height: 5px;
  }
  100% {
    height: 15px;
  }
}

.eq1 {
  height: 2px;
  left: 0;
  animation-name: shorteq;
  animation-duration: 0.5s;
  animation-iteration-count: infinite;
  animation-delay: 0s;
  animation-delay: 0s;
  transition: 3s;
}

.eq2 {
  height: 2px;
  left: 6px;
  animation-name: talleq;
  animation-duration: 0.5s;
  animation-iteration-count: infinite;
  animation-delay: 0.17s;
  animation-delay: 0.17s;
  transition: 3s;
}

.eq3 {
  height: 2px;
  left: 12px;
  animation-name: shorteq;
  animation-duration: 0.5s;
  animation-iteration-count: infinite;
  animation-delay: 0.34s;
  animation-delay: 0.34s;
  transition: 3s;
}

::selection {
  color: $color-rosa-clar;
  background: $color-blauet-clar;
}

#loadingCenter {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  opacity: 1;
}

.SUPERHOT {
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  display: none;
  padding: 10px 0;
  text-align: center;
  z-index: 1;
  position: relative;
  flex: 1;
  white-space: nowrap;
  background: transparent;
  background-repeat: repeat-x;
  background-size: 150% 700%;
  background-image: url("./images/wave.svg");
  background-position: 0 2000px;

  .superS {
    width: 100%;
    text-shadow: 0px 0px 20px #3efaff;
    color: #fff;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  #super1 {
    font-size: 18vw;
  }

  #super2,
  #super3,
  #super4,
  #super5,
  #super6 {
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    #first1,
    #first3 {
      line-height: 3px;
    }

    #first2 {
      line-height: 19.5vw;
    }
  }

  #super6 {
    font-size: 15vw;
  }
}

@keyframes fillIn {
  0% {
    background-position: 0 2000px;
  }
  100% {
    background-position: -800% -1500px;
  }
}

@keyframes superAnimation {
  0% {
    font-size: 18vw;
    background: rgba(62, 250, 255, 0.1);
  }
  100% {
    background: none;
    font-size: 15vw;
  }
}

@keyframes superAnimation2 {
  0% {
    font-size: 12vw;
    background: rgba(62, 250, 255, 0.1);
  }
  100% {
    background: none;
    font-size: 7.5vw;
  }
}

@keyframes fadeInOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    display: none;
  }
}

@keyframes colorAnim {
  0% {
    background: linear-gradient(
      180deg,
      rgba(114, 175, 204, 1) 0%,
      rgba(200, 17, 157, 1) 100%
    );
  }
  100% {
    background: #000;
  }
}

.blade {
  position: absolute;
  top: 50vh;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 5px;
  z-index: 9;
  display: none;
  justify-content: flex-start;
  align-items: center;

  .bladeP {
    animation: bladeAnim 0.7s normal forwards;
    box-shadow: 0 10px 40px -10px #fff;
    background: #fff;
    height: 5px;
    width: 0;
    -webkit-box-shadow: 0px 0px 20px 5px rgba(62, 250, 255, 1);
    -moz-box-shadow: 0px 0px 20px 5px rgba(62, 250, 255, 1);
    box-shadow: 0px 0px 20px 5px rgba(62, 250, 255, 1);
  }
}

@keyframes bladeAnim {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}

.mainBody {
  display: none;
  height: auto;
  width: 100%;
  position: absolute;
  z-index: 8;
  background: $blood;

  #bodyUp,
  #bodyDown {
    background: $blood;
    position: relative;
    width: 100%;
    position: relative;
    transform-style: preserve-3d;
    transform: translateZ(-300px);
    transition: transform 1s;

    .d3 {
      position: absolute;
      width: 100%;
      height: 100%;
    }

    #upFront {
      background: url("./images/ep_naturalblack.png");
      background-repeat: repeat;
      transform: rotateY(0deg) translateZ(300px);
    }

    #upBack {
      background: $blood;
      transform: rotateX(90deg) translateZ(300px);
    }

    #downFront {
      background: $blood;
      transform: rotateY(0deg) translateZ(300px);
    }

    #downBack {
      background: linear-gradient(
        180deg,
        rgba(20, 20, 18, 1) 26%,
        rgba(91, 32, 115, 1) 87%
      );
      transform: rotateX(90deg) translateZ(300px);
    }

    @keyframes gradient {
      0% {
        background-position: 0% 50%;
      }
      50% {
        background-position: 100% 50%;
      }
      100% {
        background-position: 0% 50%;
      }
    }
  }

  #downBack {
    position: relative !important;
  }

  #bodyUp {
    height: 50vh;
  }

  #bodyDown {
    min-height: 50vh;
  }

  @keyframes cubeRewerse {
    0% {
      transform: translateZ(-300px) rotateX(-90deg);
    }
    100% {
      transform: translateZ(-300px) rotateY(0deg);
    }
  }

  @keyframes cubeRewerse2 {
    0% {
      transform: translateZ(-300px) rotateY(0deg);
    }
    100% {
      transform: translateZ(-300px) rotateX(-90deg);
    }
  }
}
