footer {
  margin-top: 50px;
  width: 100%;
  position: relative;

  #canv {
    position: relative;
    width: 100%;
    height: 500px;
    z-index: 1;

    #footCont {
      position: absolute;
      z-index: 2;
      $shadow: -0.6rem 0.6rem 0 rgba(29, 30, 28, 0.26);
      background-color: #a2a2a2;
      display: flex;
      justify-content: center;
      flex-direction: column;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      overflow: hidden;

      input,
      button,
      textarea {
        border: 2px solid rgba(0, 0, 0, 0.6);
        background-image: none;
        background-color: #dadad3;
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
        box-shadow: none;
        padding: 5px;
        font-family: 'Roboto', sans-serif;
        line-height: 0.8;

        &:focus {
          outline: none;
        }
      }

      textarea {
        min-height: 50px;
        resize: vertical;
        max-height: 300px;
      }

      button {
        cursor: pointer;
        font-weight: 500;
      }

      .feedback {
        &-card {
          border: 1px solid black;
          max-width: 980px;
          background-color: #fff;
          margin: 0 auto;
          box-shadow: $shadow;
        }
        &-header {
          text-align: center;
          padding: 8px;
          font-size: 14px;
          font-weight: 700;
          border-bottom: 1px solid black;
          line-height: 20px;
        }
        &-body {
          padding: 20px;
          display: flex;
          flex-direction: column;

          &__message {
            margin-top: 10px;
          }

          button {
            margin-top: 10px;
            align-self: flex-end;
          }
        }
      }
    }

    canvas {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
    }
  }

  .theLastStand {
    background: #000;
    font-family: Ruda;
    display: flex;
    flex-direction: column;
    padding: 50px 65px 20px 65px;
    align-items: flex-start;

    .gitLink {
      text-decoration: none;
      font-size: 30px;
      transition: 0.6s;

      #gitHub {
        color: #f05133;
        font-size: 40px;
      }
      #spanGit {
        padding-left: 5px;
        color: #fff;
      }
    }

    .gitLink:hover {
      opacity: 0.6;
    }

    .inspiredBy {
      margin-top: 35px;
      color: #fff;
      display: flex;
      flex-direction: column;

      .insBy {
        font-size: 30px;
      }

      .aIns {
        text-decoration: none;
        color: #fff;
        font-size: 20px;
        transition: 0.6s;
        font-weight: 700;
      }

      .aIns:hover {
        opacity: 0.6;
      }
    }
    .copyright {
      margin-top: 40px;
      color: #fff;
      font-size: 20px;
    }
  }
}

@media only screen and (max-width: 480px) {
  footer {
    .theLastStand {
      padding-left: 10px;
      padding-right: 10px;
    }
  }
}
